@import 'colors';
@import 'typography';
@import 'icons';
@import 'print';
@import 'light';

* {
  box-sizing: border-box;

  &::selection {
    background-color: lighten(black, 90%);
  }
}

html,
body {
  padding: 0;
  margin: 0;
  background-color: @dark;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
  font-weight: 300;
}

body {
  &:before,
  &:after {
    content: '';
    position: fixed;
    left: 0;
    right: 0;
    height: 0;
    box-shadow: 0 0 5vh 5vh @dark, 0 0 5vh 5vh @dark;
    z-index: 10;
    background: @dark;
  }

  &:before {
    top: 0;
  }

  &:after {
    bottom: 0;
  }
}

.page {
  min-width: 300px;
  min-height: 100vh;
  color: white;
  background: @dark data-uri('./images/svg/background.svg') right repeat-y;
  background-size: contain;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  width: 100%;
  padding: 0 16px;
}

.flex {
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  &:after {
    content: '';
    display: block;
    clear: both;
  }

  .left-block {
    margin-right: auto;
    float: left;
  }

  .right-block {
    margin-left: auto;
    float: right;
  }
}

header {
  padding: 5vh 0 0;

  .contacts {
    margin: 0;

    .text {
      display: none;
    }

    li {
      list-style-type: none;
      display: inline-block;
      text-decoration: none;

      a {
        display: inline-block;
        padding: 16px;
        font-size: 16px;
        line-height: 24px;
        text-decoration: none;

        &:after {
          bottom: 8px;
        }
      }
    }
  }
}

.logo {
  display: inline-block;
  font-size: 18px;
  line-height: 24px;
  padding: 16px 16px 16px 0;
  text-decoration: none;
  transition: color 150ms ease-in-out;

  &:after {
    bottom: 6px;
    margin-left: -8px;
  }
}

.ava-box {
  margin: 32px auto;
  float: none;
  width: 240px;
}

.ava {
  border-radius: 50%;
  width: 240px;
  height: 240px;
  overflow: hidden;
  box-shadow: 0 0 30px 5px @dark, 0 0 1px 1px white, 0 0 1px 1px white;

  picture {
    display: block;
    width: 100%;
  }
}

section {
  position: relative;
}

cite {
  text-align: center;
}

.mobile-reverse {
  display: flex;
  flex-direction: column-reverse;
}

.experience {
  padding: 0 0 5vh 0;

  .table {
    border-collapse: collapse;
    display: block;
    border-left: 2px solid white;
    font-size: 16px;

    .tr {
      display: flex;
      flex-direction: column;
      padding-bottom: 32px;

      &:first-child {
        display: none;
      }

      .td {
        display: block;
        padding: 0 16px 16px;
        position: relative;

        &:first-child {
          order: 1;
        }
      }
    }
  }

  .left,
  .right,
  .year {
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      background: white;
      border-radius: 50%;
      width: 8px;
      height: 8px;
    }
  }

  .left:before {
    display: none;
    right: -5px;
  }

  .right:before {
    display: none;
    left: -5px;
  }

  .year:before {
    display: block;
    left: -5px;
    top: 12px;
  }

  .year {
    width: auto;
    min-height: 16px;
    padding-top: 8px;
    text-align: left;
    font-weight: 300;
    font-size: 18px;

    .icon_merge {
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 16px;
      display: none;
    }
  }

  .title {
    font-size: 120%;
  }

  .description {
    font-size: 80%;
  }
}

.hidden-md {
  display: table-row;
}

.skills {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-wrap: wrap;

  ul {
    margin: 0 100px 0 0;
    padding-left: 20px;

    li {
      font-size: 22px;
      font-weight: 300;
      margin-bottom: 16px;
    }
  }
}
.projects {
  list-style: none;
  margin: 0;
  padding: 0;
  .project {
    border-left: 2px solid white;
    padding: 0 0 0 16px;
    margin: 5vh 0 10vh;
    position: relative;
    display: block;

    h3 {
      font-family: inherit;
      padding-bottom: 24px;
    }

    h5 {
      font-family: inherit;
      padding-bottom: 8px;
    }

    .description {
      &:after {
        content: '';
        display: block;
        clear: both;
      }
    }

    .image {
      margin: 32px 0;

      a {
        display: block;
        border: none;

        &:after {
          bottom: -10px;
        }
      }

      picture {
        max-height: 420px;
      }
    }
  }
}

picture {
  display: block;
  max-width: 100%;

  img {
    max-width: 100%;
    display: block;
  }
}

@media (min-width: 600px) {
  h3 {
    font-size: 30px;
  }

  .container {
    max-width: 600px;
  }

  cite {
    text-align: left;
    position: absolute;
    top: 40px;
    left: 260px;
    right: 0;
  }

  .about {
    position: relative;

    table {
      margin-top: 50px;
    }
  }

  .ava-box {
    margin: 0 32px 0 0;
    float: none;
  }
  .projects {
    .project {
      border-left: none;

      &:hover {
        .description {
          opacity: 1;
        }

        .image {
          picture {
            opacity: 1;
            filter: grayscale(0);
          }
        }
      }

      h3 {
        padding-left: 332px;
        text-transform: none;
      }

      .description {
        transition: opacity 150ms ease-in-out;
        padding-left: 332px;
        opacity: 0.5;
      }

      .image {
        position: absolute;
        top: 0;
        left: 0;
        width: 320px;
        margin: 0;

        picture {
          transition: all 150ms ease-in-out;
          opacity: 0.7;
          filter: grayscale(1);
        }
      }
    }
  }

  .experience {
    .table {
      display: table;
      border: none;

      .tr {
        display: table-row;
        padding: 0;

        &:first-child {
          display: table-row;
        }

        .td {
          display: table-cell;
          vertical-align: top;
          padding: 0 16px 16px;

          &:first-child {
            padding-left: 0;

            &:empty {
              border-color: fade(white, 20%);
            }
          }

          &:last-child {
            padding-right: 0;
          }

          &.empty {
            border-color: transparent !important;
          }

          &:first-child {
            border-right: 2px solid fade(white, 50%);
          }

          &:last-child {
            border-left: 2px solid fade(white, 50%);
          }
        }
      }
    }

    .left:before,
    .right:before {
      display: block;
    }

    .left,
    .right {
      margin-top: -4px;
    }

    .year {
      text-align: center;
      width: 70px;
      min-height: 88px;
      padding-top: 0;
      margin: -4px 0 0 0;

      &:before {
        display: none;
      }

      .icon_merge {
        width: 104px;
        height: 104px;
        display: block;
      }
    }
  }
}

@media (min-width: 900px) {
  .container {
    max-width: 900px;
  }

  cite {
    position: static;
  }

  .ava-box {
    float: left;
  }

  .mobile-reverse {
    flex-direction: column;
  }

  header {
    .contacts {
      .text {
        display: inline;
      }
    }
  }

  .hidden-md {
    display: none;
  }

  .icon.icon_location {
    width: 14px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}
